.TopMenu{
    width:100%;
    height: 70px;
    margin: 0;
    padding: 0;
    position: fixed;
    top: 0px;
    overflow: hidden;
    background-color: #333;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
    float: left;
}

li{
    display: inline;
    vertical-align:middle;
}

.logo{
    height: 70px;
}

.menubutton{
    color: white;
    height: 100%;
    background-color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border: none;
}

.menubutton:hover{
    background-color: #333;
}