.Modal{
    position: absolute;
    top: 35%;
    left: 35%;
    background-color: white;
    border: medium solid black ;
    border-radius: 10%;
    width: 30%;
    height: 30%;
    padding: 10px
}

.radio{
    color: black;
    text-align: left;
}

.mask{
    position: absolute;
    height:100%;
    width:100%;
    top: 50%;
    left: 50%
}

.filterbutton{
    color: white;
    background-color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border: none;
}
