.AddAtracao{
    position: absolute;
    top: 10%;
    left: 10%;
    background-color: white;
    border: medium solid black ;
    border-radius: 3%;
    width: 80%;
    height: 80%;
    padding: 0px;
    color:black;
    text-align: left;
    z-index: 100;
}

.maskAtracao{
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0px;
    left: 0px
}

.closeButton{
    position: absolute;
    right: 20px;
    top: 20px;
    background: none;
    border: none;
}