.AtracaoContainer{
    width: 200px;
    border: medium solid black;
    border-radius: 6px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

@media only screen and (max-width: 250px){
    .AtracaoContainer{
        width: 100%;
        box-shadow: none;
    }
}

.Atracao{
    width: 100%;
}

.AtracaoContainer:hover{
    border-color: white;
    background-color: grey;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.nome{
    color: black;
    font-size: medium;
    font-weight: bold;
    text-align: left;
    padding-left: 10px;
}
.date{
    color: black;
    font-size: small;
    text-align: left;
    padding-left: 10px;
}
.tipo{
    color: black;
    font-size: small;
    text-align: left;
    padding-left: 10px;
}

.likedContainer{
    position:relative;
}

.liked{
    width:64px;
    z-index: 1;
    position: absolute;
    right: 10px;
    bottom: 10px;
}

